import { Link } from 'gatsby'
import get from 'lodash/get'
import React from 'react'
import map from 'lodash/map'
import Img from 'gatsby-image'
import ScrollAnimation from 'react-animate-on-scroll';

import Adsense from 'components/Adsense'
import './style.scss'

import ScrollToTop from "react-scroll-up";

const Post = ({ data, options }) => {
  const {
    category,
    role,
    skill,
    description,
    title,
    path,
    date,
    image,
  } = data.frontmatter
  const { isIndex, adsense } = options
  const html = get(data, 'html')
  const isMore = isIndex && !!html.match('<!--more-->')
  const fluid = get(image, 'childImageSharp.fluid')

  return (

  <div className="article" key={path}>
    <div className="container-fluid mas-post">
      <div className="row mb-4">
        <div className="offset-md-1 col-md-10">
            <div className="info pr-3">
              <p className="category">{category}</p>
                <h1>{title}</h1>
            </div>
        </div>
      </div>
      <div className="row mb-4">
        <div className="offset-md-1 col-lg-5 col-md-6">
        <ScrollAnimation animateIn='fadeInUp' offset={0} duration={0.6} animateOnce={true}>
          <div className="info pr-3">
            <p className="opening pt-lg-1 pb-lg-3">{description}</p>
            <hr/>
            <b>Year</b><br/><time dateTime={date}>{date}</time> <br/>
            <b>My Roles</b><br/>{Role({ items: role })}<br/>
            <b>Skills</b><br/>{Skill({ items: skill })}<br/>

          </div>
        </ScrollAnimation>
        </div>
        <div className="offset-lg-1 col-md-4">
        <ScrollAnimation animateIn='fadeIn' offset={0} duration={0.6} animateOnce={true}>
            {fluid ? (
              <Img fluid={fluid} style={{ display: 'block', margin: '0 auto' }} />
            ) : (
              ''
            )}
        </ScrollAnimation>
        </div>
      </div>
    </div>
    <div className="article-content bg-white" key={path}>
      <div className="container">
        <div className="row mt-5">
          <div className="col-md-10 mx-auto">  
            <div
              className="content"
              dangerouslySetInnerHTML={{
                __html: isMore ? getDescription(html) : html,
              }}
            />
            {isMore ? Button({ path, label: 'MORE', primary: true }) : ''}
            {getAd(isIndex, adsense)}    
            <ScrollToTop showUnder={360}>
              <span className="arrow">â†‘</span>
            </ScrollToTop>
          </div> 
        </div>
        
      </div>
    </div>
    <div className="article bg-light-main py-5">
      <div className="container">
        <div className="row">
          <div className="col"> 
          <Link className="text-center" to="/"><h4>Back to Index</h4></Link>
          </div>
        </div>
      </div>
    </div>
  </div> 
  )
}

export default Post

const getAd = (isIndex, adsense) => {
  return !isIndex ? <Adsense clientId={adsense} slotId="" format="auto" /> : ''
}

const getDescription = body => {
  body = body.replace(/<blockquote>/g, '<blockquote class="blockquote">')
  if (body.match('<!--more-->')) {
    body = body.split('<!--more-->')
    if (typeof body[0] !== 'undefined') {
      return body[0]
    }
  }
  return body
}

const Button = ({ path, label, primary }) => (
  <Link className="readmore" to={path}>
    <span
      className={`btn btn-outline-primary btn-block ${
        primary ? 'btn-outline-primary' : 'btn-outline-secondary'
      }`}
    >
      {label}
    </span>
  </Link>
)

const Role = ({ items}) =>
  map(items, (item, i) => {
    return (
      <span
        className={`attr_role`}
        key={i}
      >
        {item}
      </span>
    )
  })

  const Skill = ({ items}) =>
  map(items, (item, i) => {
    return (
      <span
        className={`attr_skill`}
        key={i}
      >
        {item}
      </span>
    )
  })
